import LotteryDraw from '@/components/LotteryDraw.vue';

export default {
  name: 'App',
  components: { LotteryDraw },
  data() {
    return {
      user: {
        username: '',
        password: ''
      },
      userList: [],
      id: 1,
      delNum: 1,
    };
  },
  methods: {
    onSubmit() {
      if(!this.user.username) {
        alert('请输入用户名');
      } else if(!this.user.password) {
        alert('密码不能为空!');
      } else {
        let user1 = {id: this.id, ...this.user};
        let data1 = JSON.parse(JSON.stringify(user1));
        this.userList.push(data1);
        this.id++;
        console.log(this.userList, this.id);
      }
    },
    delData() {
      let index = this.delNum-1;
      if(this.userList[index] && index>=0) {
        this.userList.splice(index,1);
      }
    },
  },
}
